input{
    width : 70%;
    padding:10px 20px;
    margin : 10px 130px;
    text-align: center;
    font-size: 20px;
}
.mainDiv{
    margin-bottom: 0px;
}
input.error {
    border: 2px solid red;
    color: red;
  }
.cardContainer{
 /* display: grid;
 grid-template-columns: auto auto auto auto auto;
 grid-gap: 15px;
 padding:20px 30px;
 margin:10px 20px;
} */
display: grid;
grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)) ;
grid-gap: 15px;  
padding: 0 100px 50px;
}
.card{
    width:100%;
    position: relative;
    
}
.card img{
    width:100%;
    height: 80%;
}
.card p{
    background-color:darkgray;
    padding: 10px;
    font-size: 20px;
    position:absolute;
    bottom:20%;
    margin:0px;
    width:100%;
    box-sizing: border-box;
    text-align: center;
    display: none;
}
.card:hover  p {
    display: block;
}